@font-face {
  font-family: Arial;
  font-display: swap;
  src: url("../fonts/Arial-Regular.woff") format("woff"), url("../fonts/Arial-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  overflow-wrap: break-word;
  user-select: text;
}

body {
  margin: 0;
  font-family: Arial, sans-serif;
  font-style: normal;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
}

button {
  padding: 0;
  border: none;
  cursor: pointer;
  outline: none;
}

ol,
ul {
  padding-left: 0;
}

dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

.containers {
  max-width: 1440px;
  padding: 0 90px;
  margin: 0 auto;
  box-sizing: border-box;
}

.display-n {
  display: none !important;
}

opacity-h {
  opacity: 0 !important;
}

.header {
  width: 100%;
  min-height: 70px;
  background: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 0px;
  flex-wrap: nowrap;
}

.header .containers {
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  gap: 30px;
  flex-wrap: nowrap;
  width: 100%;
}

.header__block {
  width: max-content;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  gap: 0px;
  flex-wrap: nowrap;
}

.header__block:first-child {
  justify-content: flex-start;
  gap: 32px;
}

.header__block:last-child {
  justify-content: flex-end;
  gap: 30px;
}

.header__logo {
  cursor: pointer;
}

.header__subtitle {
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: #262626;
  font-style: normal;
}

.header__email {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 11px;
  flex-wrap: nowrap;
}

.header__email p {
  font-weight: 400;
  font-size: 15px;
  line-height: 19px;
  color: #140848;
  font-style: normal;
  letter-spacing: -0.5px;
}

.header__phone {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 11px;
  flex-wrap: nowrap;
}

.header__phone p {
  font-weight: 400;
  font-size: 15px;
  line-height: 19px;
  color: #140848;
  font-style: normal;
}

.header__button {
  background: #f2ce14;
  border-radius: 10px;
  padding: 10px 17px;
  cursor: pointer;
}

.header__button p {
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: #141413;
  font-style: normal;
  text-transform: uppercase;
}

.headerCitySelect {
  padding: 7px 5px 7px 25px;
  border-radius: 30.5px;
  background-color: #ffffff;
  width: 75px;
  position: relative;
  box-sizing: content-box;
}

.headerCitySelect::after {
  content: "";
  background: url("./../svg/header/arrow-down-blue-dark.svg") no-repeat;
  transition: all 0.5s ease;
  position: absolute;
  top: 50%;
  right: -8px;
  transform: translate(0, -50%);
  height: 11px;
  width: 14px;
}

.headerCitySelect::before {
  content: "";
  width: 24px;
  height: 24px;
  position: absolute;
  top: 50%;
  left: 0;
  background-image: url("./../svg/header/geo.svg");
  transform: translate(0, -50%);
}

.headerCitySelect-placeholder {
  display: block;
  padding: 5px 0;
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
  font-weight: 500;
  font-size: 15px;
  line-height: 19px;
  color: #140848;
  font-style: normal;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  white-space: nowrap;
  box-sizing: content-box;
}

.headerCitySelect-block {
  bottom: auto;
  top: 100%;
  position: absolute;
  left: 0;
  z-index: 5;
  box-sizing: content-box;
}

.headerCitySelect-wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 0px;
  flex-wrap: nowrap;
  height: max-content;
  max-height: 120px;
  overflow-y: auto;
}

.headerCitySelect-wrapper::-webkit-scrollbar {
  width: 27px;
}

.headerCitySelect-wrapper::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 10px 10px #808080;
  box-shadow: inset 0 0 10px 10px #808080;
  border: solid 12px transparent;
}

.headerCitySelect-wrapper::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 10px 10px #141413;
  box-shadow: inset 0 0 10px 10px #141413;
  border: solid 10px transparent;
}

.headerCitySelect-element {
  width: 100%;
  padding: 3px 0;
  background: 0 0;
  color: #141413;
  border: none;
  margin: 0 0 5px 0;
  text-align: left;
  cursor: pointer;
}

.headerCitySelect-element-active {
  background: #ffffff;
}

.headerCitySelect-item-active::after {
  transition: all 0.5s ease;
  transform: translate(0, -50%) rotate(180deg);
}

.headerCitySelect-item-active .headerCitySelect-block {
  padding: 15px 0 15px 15px;
  width: 100px;
  height: auto;
  border-radius: 15px;
  background-color: #ffffff;
  border: 1px solid #141413;
}

