}

.indexMain .swiper-root {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  position: absolute;
  height: max-content;
  z-index: 55;
}

.indexMain .swiper-slide {
  position: relative;
  width: 100%;
  height: calc(100vh - 70px);
}

.indexMain .swiper-slide .containers {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 0px;
  flex-wrap: nowrap;
  padding: 0;
  position: relative;
}

.indexMain .swiper-controller {
  z-index: 50;
  width: 602px;
  height: 92px;
  background: #201547;
  border-radius: 20px;
  margin: 27px auto 0 auto;
  position: relative;
}

.indexMain .swiper-button-next {
  top: 50% !important;
  right: 68px !important;
  left: auto !important;
  bottom: auto !important;
  transform: translate(0, -50%);
  width: 55px !important;
  height: 24px !important;
  margin: 0 !important;
}

.indexMain .swiper-button-next::after {
  content: "" !important;
}

.indexMain .swiper-button-next::before {
  content: "";
  position: absolute;
  top: 50%;
  right: -25px;
  border: 1px solid #f2ce14;
  border-radius: 50%;
  width: 53px;
  height: 53px;
  transform: translate(0, -50%);
  transition: all 0.5s ease;
  opacity: 1;
}

.indexMain .swiper-button-next svg path {
  transition: all 0.5s ease;
}

.indexMain .swiper-button-prev {
  top: 50% !important;
  left: 68px !important;
  right: auto !important;
  bottom: auto !important;
  transform: translate(0, -50%);
  width: 55px !important;
  height: 24px !important;
  margin: 0 !important;
}

.indexMain .swiper-button-prev::after {
  content: "" !important;
}

.indexMain .swiper-button-prev::before {
  content: "";
  position: absolute;
  top: 50%;
  left: -25px;
  border: 1px solid #f2ce14;
  border-radius: 50%;
  width: 53px;
  height: 53px;
  transform: translate(0, -50%);
  transition: all 0.5s ease;
  opacity: 1;
}

.indexMain .swiper-button-prev svg path {
  transition: all 0.5s ease;
}

.indexMain .swiper-button-disabled::before {
  opacity: 0;
}

.indexMain .swiper-button-disabled svg path {
  fill: #505050;
}

.indexMain .swiper-pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 17px;
  flex-wrap: nowrap;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) !important;
}

.indexMain .swiper-pagination-fraction {
  position: absolute;
  top: 61px;
  left: 0px !important;
  width: max-content !important;
  height: max-content;
  justify-items: right;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas: "current current" "current current" ". total";
}

.indexMain .swiper-pagination-item {
  width: 13px;
  height: 13px;
  border: 1px solid #f2ce14;
  background-color: transparent;
  transition: all 0.5s ease;
  border-radius: 50%;
  cursor: pointer;
}

.indexMain .swiper-pagination-item-active {
  background-color: #f2ce14;
}

.indexMain .swiper-pagination-current {
  text-transform: capitalize;
  -webkit-text-stroke: 2px #505050;
  font-weight: 500;
  font-size: 128px;
  line-height: 151px;
  color: transparent;
  font-style: normal;
  grid-area: current;
}

.indexMain .swiper-pagination-total {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #a5a5a5;
  font-style: normal;
  letter-spacing: -0.5px;
  text-transform: capitalize;
  grid-area: total;
  margin-right: 16px;
  position: relative;
  height: max-content;
}

.indexMain .swiper-pagination-total::after {
  content: "/";
  position: absolute;
  top: 50%;
  left: -12px;
  transform: translate(0, -50%);
}

.indexMain .swiper-counter {
  letter-spacing: -0.5px;
  text-transform: capitalize;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: transparent;
  font-style: normal;
}

